<template>
    <v-container >
        <v-card
            width="1250"
            class="mx-auto px-2 elevation-0 pa-2"
            height="100%"
            outlined
        >
            <form >
                <v-text-field
                    v-model="name"
                    :error-messages="nameErrors"
                    :counter="10"
                    label="  /  "
                    required
                    @input="$v.name.$touch()"
                    @blur="$v.name.$touch()"
                ></v-text-field>
                <v-text-field
                    v-model="phone"
                    :error-messages="phoneErrors"
                    label=""
                    required
                    @input="$v.phone.$touch()"
                    @blur="$v.phone.$touch()"
                ></v-text-field>
                <v-text-field
                    v-model="startDate"
                    :error-messages="startDateErrors"
                    label=" "
                    type="date"
                    required
                    @input="$v.startDate.$touch()"
                    @blur="$v.startDate.$touch()"
                ></v-text-field>
                <v-text-field
                    v-model="startTime"
                    :error-messages="startTimeErrors"
                    label=" "
                    type="time"
                    required
                    @input="$v.startTime.$touch()"
                    @blur="$v.startTime.$touch()"
                ></v-text-field>
                <v-text-field
                    v-model="useTime"
                    :error-messages="useTimeErrors"
                    label=" "
                    type="number"
                    min="1"
                    max="5"
                    required
                    @input="$v.useTime.$touch()"
                    @blur="$v.useTime.$touch()"
                ></v-text-field>
                <v-select
                    v-model="room"
                    :items="rooms"
                    :error-messages="roomsErrors"
                    label=""
                    required
                    @change="$v.room.$touch()"
                    @blur="$v.room.$touch()"
                ></v-select>
                <p class="font-weight-black"> </p>
                <p class="subtitle-2">&#12685;   /   5     /     .</p>
                <p class="subtitle-2">&#12685;  <strong>010-4444-4444</strong>    . </p>
                <p class="subtitle-2">&#12685;   '<strong></strong>'  . </p>
                <v-checkbox
                    v-model="checkbox"
                    :error-messages="checkboxErrors"
                    label=" "
                    required
                    @change="$v.checkbox.$touch()"
                    @blur="$v.checkbox.$touch()"
                ></v-checkbox>
                
                <v-btn
                    class="mr-4 "
                    color="primary"
                    block
                    @click="submit"
                >
                  
                </v-btn>
            </form>
        </v-card>
    </v-container>
</template>

<script>
  import schedule from '../services/schedule'

  import { validationMixin } from 'vuelidate'
  import { required, maxLength ,numeric } from 'vuelidate/lib/validators'

  export default {
    mixins: [validationMixin],

    validations: {
      name: { required, maxLength: maxLength(10) },
      phone: { required },
      room: { required },
      startDate: { required },
      useTime: { required ,numeric },
      startTime:{required },
      checkbox: {
        checked (val) {
          return val
        },
      },

    },

    data: () => ({
      name:'',
      phone:'',
      room: null,
      startDate:'',
      startTime:'',
      useTime:1,
      rooms: [
        'A Room',
        'B Room',
        'C Room',
      ],
      checkbox: false,
    }),

    computed: {
      checkboxErrors () {
        const errors = []
        if (!this.$v.checkbox.$dirty) return errors
        !this.$v.checkbox.checked && errors.push('  ')
        return errors
      },
      roomsErrors () {
        const errors = []
        if (!this.$v.room.$dirty) return errors
        !this.$v.room.required && errors.push(' ')
        return errors
      },
      nameErrors () {
        const errors = []
        if (!this.$v.name.$dirty) return errors
        !this.$v.name.maxLength && errors.push(' 10   ')
        !this.$v.name.required && errors.push('')
        return errors
      },
      startDateErrors () {
        const errors = []
        if (!this.$v.startDate.$dirty) return errors
        !this.$v.startDate.required && errors.push('')
        return errors
      },
      phoneErrors () {
        const errors = []
        if (!this.$v.phone.$dirty) return errors
        !this.$v.phone.required && errors.push('')
        return errors
      },
      startTimeErrors () {
        const errors = []
        if (!this.$v.startTime.$dirty) return errors
        !this.$v.startTime.required && errors.push('')
        return errors
      },

      useTimeErrors () {
        const errors = []
        if (!this.$v.useTime.$dirty) return errors
        !this.$v.useTime.numeric && errors.push('  .')
        !this.$v.useTime.required && errors.push('')
        return errors
      },

    },

    methods: {
      submit () {
        this.$v.$touch();

        if(process.env.VUE_APP_ADMIN === this.name && process.env.VUE_APP_PHONE === this.phone )  this.$router.push('user' )

        if (this.$v.$invalid) {
          return;
        }else{
          let reservation = { 'name':this.name , 'phone':this.phone , 'room':this.room , 'startDate':this.startDate , 'startTime':this.startTime, 'useTime': this.useTime , 'room': this.room}
          schedule.add(reservation)
          alert(' . \n   .')
        }
      },

      clear () {
        this.$v.$reset()
        this.name = ''
        this.phone = ''
        this.room = null
        this.startDate = ''
        this.useTime = 1
        this.startTime = null
        this.checkbox = false
      },
    },
  }
</script>